.form-js{
    width: 400px;
    border-radius: 20px;
    background-color: antiquewhite;
    padding: 40px;
    outline: 1px solid gray;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 0 auto;
}

.input-name,.input-email,.input-tel{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    outline: 1px solid gray;
    border: none;
    padding-left: 20px;
}

.coment-js{
    width: 100%;
    height: 100px;
    resize: none;
    outline: 1px solid gray;
    padding-left: 20px;
    border: none;
}

.btn-submit{
    width: 200px;
    height: 50px;
    background-color: blue;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    color: white;
    display: block;
    margin: 0 auto;
}